"use client";

import { useState, useEffect } from "react";
import {
  Users,
  Trash2,
  Search,
  User,
  Store,
  AlertTriangle,
  CheckCircle,
  BarChart2,
  Settings,
  LogOut,
} from "lucide-react";
import { Montserrat } from "next/font/google";
import Link from "next/link";
import { usePathname } from "next/navigation";

const montserrat = Montserrat({
  subsets: ["latin"],
  weight: ["800", "400"],
});

interface Client {
  id: number;
  firstName: string;
  lastName: string;
  email: string;
  createdAt: string;
  phone?: string;
  address?: {
    street: string;
    province: string;
    postalCode: string;
  };
  image?: string;
}

interface Seller {
  id: number;
  firstName: string;
  lastName: string;
  email: string;
  createdAt: string;
  shopName: string;
  phone?: string;
  address?: {
    street: string;
    province: string;
    postalCode: string;
  };
  reports?: number;
  image?: string;
}

export default function UsersPage() {
  const [clients, setClients] = useState<Client[]>([
    {
      id: 1,
      firstName: "Ahmed",
      lastName: "Ben Salah",
      email: "ahmed.bensalah@example.com",
      createdAt: "2024-01-15",
      phone: "+216 20 123 456",
      address: { street: "Rue Tunis", province: "Tunis", postalCode: "1000" },
      image: "https://randomuser.me/api/portraits/men/1.jpg",
    },
    {
      id: 2,
      firstName: "Fatima",
      lastName: "Zohra",
      email: "fatima.zohra@example.com",
      createdAt: "2024-03-22",
      phone: "+216 21 456 789",
      address: { street: "Avenue Habib", province: "Sousse", postalCode: "4000" },
      image: "https://randomuser.me/api/portraits/women/2.jpg",
    },
    {
      id: 3,
      firstName: "Youssef",
      lastName: "Khalil",
      email: "youssef.khalil@example.com",
      createdAt: "2024-05-10",
      phone: "+216 23 789 123",
      address: { street: "Rue Kairouan", province: "Kairouan", postalCode: "3100" },
      image: "https://randomuser.me/api/portraits/men/3.jpg",
    },
  ]);

  const [sellers, setSellers] = useState<Seller[]>([
    {
      id: 1,
      firstName: "Karim",
      lastName: "Haddad",
      email: "karim.haddad@example.com",
      createdAt: "2023-11-05",
      shopName: "Karim Fashion",
      phone: "+216 22 987 654",
      address: { street: "Avenue Carthage", province: "Sfax", postalCode: "3000" },
      reports: 2,
      image: "https://randomuser.me/api/portraits/men/2.jpg",
    },
    {
      id: 2,
      firstName: "Leila",
      lastName: "Mansour",
      email: "leila.mansour@example.com",
      createdAt: "2024-02-18",
      shopName: "Leila Boutique",
      phone: "+216 24 654 321",
      address: { street: "Rue Jendouba", province: "Jendouba", postalCode: "8100" },
      reports: 0,
      image: "https://randomuser.me/api/portraits/women/1.jpg",
    },
  ]);

  const [clientSearch, setClientSearch] = useState("");
  const [sellerSearch, setSellerSearch] = useState("");
  const [showDeleteModal, setShowDeleteModal] = useState(false);
  const [userToDelete, setUserToDelete] = useState<{
    id: number;
    type: "client" | "seller";
  } | null>(null);
  const [successMessage, setSuccessMessage] = useState<string | null>(null);
  const [selectedUser, setSelectedUser] = useState<Client | Seller | null>(null);
  const [isSidebarOpen, setIsSidebarOpen] = useState(false);

  const filteredClients = clients.filter(
    (client) =>
      client.firstName.toLowerCase().includes(clientSearch.toLowerCase()) ||
      client.lastName.toLowerCase().includes(clientSearch.toLowerCase())
  );

  const filteredSellers = sellers.filter(
    (seller) =>
      seller.firstName.toLowerCase().includes(sellerSearch.toLowerCase()) ||
      seller.lastName.toLowerCase().includes(sellerSearch.toLowerCase()) ||
      seller.shopName.toLowerCase().includes(sellerSearch.toLowerCase())
  );

  const handleDeleteUser = (id: number, type: "client" | "seller") => {
    setUserToDelete({ id, type });
    setShowDeleteModal(true);
  };

  const confirmDelete = () => {
    if (userToDelete) {
      if (userToDelete.type === "client") {
        setClients(clients.filter((client) => client.id !== userToDelete.id));
        setSuccessMessage("Client supprimé avec succès !");
      } else {
        setSellers(sellers.filter((seller) => seller.id !== userToDelete.id));
        setSuccessMessage("Vendeur supprimé avec succès !");
      }
      setShowDeleteModal(false);
      setUserToDelete(null);
    }
  };

  const cancelDelete = () => {
    setShowDeleteModal(false);
    setUserToDelete(null);
  };

  const handleUserClick = (user: Client | Seller) => {
    setSelectedUser(user);
  };

  const closeUserDetails = () => {
    setSelectedUser(null);
  };

  const handleWarnSeller = (id: number, sellerName: string) => {
    setSuccessMessage(`Avertissement envoyé à ${sellerName} avec succès !`);
  };

  const handleLogout = () => {
    console.log("Déconnexion...");
    // Ajoutez votre logique de déconnexion ici
  };

  useEffect(() => {
    if (successMessage) {
      const timer = setTimeout(() => {
        setSuccessMessage(null);
      }, 3000);
      return () => clearTimeout(timer);
    }
  }, [successMessage]);

  const pathname = usePathname();

  const adminData = {
    email: "admin@example.com",
    image: "", // Ajoutez une URL si nécessaire
  };

  return (
    <div className="flex min-h-screen">
      {/* 
        SIDEBAR: Barre latérale blanche avec animation fluide
        - Ouvre/ferme en cliquant n'importe où sauf sur les boutons
        - Fond blanc avec bordure grise
      */}
      <aside
        onClick={() => setIsSidebarOpen(!isSidebarOpen)} // Ouvre/ferme en cliquant sur la sidebar
        className={`fixed top-0 left-0 h-full bg-white border-r border-gray-300 shadow-md z-20 transition-all duration-500 ease-in-out ${
          isSidebarOpen ? "w-64" : "w-16"
        }`}
      >
        <div className="h-full flex flex-col justify-between">
          {/* 
            SECTION PROFIL: Icône ou image de l'admin avec email
            - Centré avec padding ajusté
          */}
          <div className="flex flex-col items-center p-4 border-b border-gray-300">
            {adminData.image ? (
              <img
                src={adminData.image}
                alt="Admin"
                className="w-10 h-10 rounded-full object-cover"
              />
            ) : (
              <User className="w-10 h-10 text-black" />
            )}
            {isSidebarOpen && (
              <span className={`mt-2 text-sm text-black ${montserrat.className}`}>
                {adminData.email}
              </span>
            )}
          </div>

          {/* 
            NAVIGATION: Liens avec icônes
            - Cercle noir quand fermée, carré quand ouverte et actif
            - stopPropagation pour éviter l'ouverture/fermeture au clic
          */}
          <nav className="flex-1 mt-4">
            <ul className="space-y-2">
              <li>
                <Link
                  href="/admin/dashboard"
                  onClick={(e) => e.stopPropagation()} // Empêche l'ouverture/fermeture
                  className={`flex items-center gap-3 p-4 transition-all duration-300 text-black ${
                    isSidebarOpen
                      ? pathname === "/admin/dashboard"
                        ? "bg-black text-white"
                        : "hover:border-l-4 hover:border-black"
                      : pathname === "/admin/dashboard"
                      ? "bg-black text-white rounded-full"
                      : "hover:bg-gray-200 rounded-full"
                  }`}
                >
                  <BarChart2 className="h-6 w-6" />
                  {isSidebarOpen && (
                    <span className={`text-lg font-semibold ${montserrat.className}`}>
                      Dashboard
                    </span>
                  )}
                </Link>
              </li>
              <li>
                <Link
                  href="/admin/gestion"
                  onClick={(e) => e.stopPropagation()} // Empêche l'ouverture/fermeture
                  className={`flex items-center gap-3 p-4 transition-all duration-300 text-black ${
                    isSidebarOpen
                      ? pathname === "/admin/gestion"
                        ? "bg-black text-white"
                        : "hover:border-l-4 hover:border-black"
                      : pathname === "/admin/gestion"
                      ? "bg-black text-white rounded-full"
                      : "hover:bg-gray-200 rounded-full"
                  }`}
                >
                  <Users className="h-6 w-6" />
                  {isSidebarOpen && (
                    <span className={`text-lg font-semibold ${montserrat.className}`}>
                      Gestion
                    </span>
                  )}
                </Link>
              </li>
              <li>
                <Link
                  href="/admin/store"
                  onClick={(e) => e.stopPropagation()} // Empêche l'ouverture/fermeture
                  className={`flex items-center gap-3 p-4 transition-all duration-300 text-black ${
                    isSidebarOpen
                      ? pathname === "/admin/store"
                        ? "bg-black text-white"
                        : "hover:border-l-4 hover:border-black"
                      : pathname === "/admin/store"
                      ? "bg-black text-white rounded-full"
                      : "hover:bg-gray-200 rounded-full"
                  }`}
                >
                  <Store className="h-6 w-6" />
                  {isSidebarOpen && (
                    <span className={`text-lg font-semibold ${montserrat.className}`}>
                      Magasin
                    </span>
                  )}
                </Link>
              </li>
              <li>
                <Link
                  href="/admin/settings"
                  onClick={(e) => e.stopPropagation()} // Empêche l'ouverture/fermeture
                  className={`flex items-center gap-3 p-4 transition-all duration-300 text-black ${
                    isSidebarOpen
                      ? pathname === "/admin/settings"
                        ? "bg-black text-white"
                        : "hover:border-l-4 hover:border-black"
                      : pathname === "/admin/settings"
                      ? "bg-black text-white rounded-full"
                      : "hover:bg-gray-200 rounded-full"
                  }`}
                >
                  <Settings className="h-6 w-6" />
                  {isSidebarOpen && (
                    <span className={`text-lg font-semibold ${montserrat.className}`}>
                      Paramètres
                    </span>
                  )}
                </Link>
              </li>
            </ul>
          </nav>

          {/* 
            BOUTON DE DÉCONNEXION: En rouge, en bas
            - Cercle noir quand fermée, carré quand ouverte et actif
            - stopPropagation pour éviter l'ouverture/fermeture
          */}
          <div className="p-4">
            <button
              onClick={(e) => {
                e.stopPropagation(); // Empêche l'ouverture/fermeture
                handleLogout();
              }}
              className={`flex items-center gap-3 p-4 w-full text-red-500 transition-all duration-300 ${
                isSidebarOpen
                  ? pathname === "/logout"
                    ? "bg-black text-white"
                    : "hover:border-l-4 hover:border-black"
                  : pathname === "/logout"
                  ? "bg-black text-white rounded-full"
                  : "hover:bg-gray-200 rounded-full"
              }`}
            >
              <LogOut className="h-6 w-6" />
              {isSidebarOpen && (
                <span className={`text-lg font-semibold ${montserrat.className}`}>
                  Déconnexion
                </span>
              )}
            </button>
          </div>
        </div>
      </aside>

      {/* Contenu principal */}
      <div
        className={`flex-1 transition-all duration-500 ease-in-out ${
          isSidebarOpen ? "ml-64" : "ml-16"
        } bg-gradient-to-br from-gray-50 to-gray-200 py-12 px-4 sm:px-6 lg:px-8 relative`}
      >
        <div className="max-w-7xl mx-auto">
          {successMessage && (
            <div className="fixed top-4 right-4 z-50">
              <div className="bg-green-600 text-white px-6 py-3 rounded-lg shadow-2xl flex items-center gap-3 transform transition-all duration-500 ease-in-out animate-bounce-in">
                <CheckCircle className="h-6 w-6" />
                <span className="font-semibold">{successMessage}</span>
                <button
                  onClick={() => setSuccessMessage(null)}
                  className="text-white hover:text-gray-200 transition-colors duration-200"
                >
                  ✕
                </button>
              </div>
            </div>
          )}

          <div className="mb-10 flex flex-col lg:flex-row lg:items-center lg:justify-between gap-6">
            <div className="flex items-center gap-3">
              <Users className="h-8 w-8 text-black" />
              <h1 className={`text-4xl font-extrabold text-gray-900 tracking-tight ${montserrat.className}`}>
                Gestion des utilisateurs
              </h1>
            </div>
            <div className="flex flex-col sm:flex-row gap-4">
              <div className="group bg-white/30 backdrop-blur-lg border border-gray-200/50 p-6 rounded-xl shadow-xl transform transition-all duration-300 hover:-translate-y-2 hover:shadow-2xl">
                <div className="flex items-center gap-4">
                  <User className="h-10 w-10 text-blue-500 group-hover:scale-110 transition-transform duration-300" />
                  <div>
                    <h3 className="text-lg font-semibold text-gray-800">Total Clients</h3>
                    <p className="text-2xl font-bold text-gray-900 mt-1">{clients.length}</p>
                  </div>
                </div>
              </div>
              <div className="group bg-white/30 backdrop-blur-lg border border-gray-200/50 p-6 rounded-xl shadow-xl transform transition-all duration-300 hover:-translate-y-2 hover:shadow-2xl">
                <div className="flex items-center gap-4">
                  <Store className="h-10 w-10 text-purple-500 group-hover:scale-110 transition-transform duration-300" />
                  <div>
                    <h3 className="text-lg font-semibold text-gray-800">Total Vendeurs</h3>
                    <p className="text-2xl font-bold text-gray-900 mt-1">{sellers.length}</p>
                  </div>
                </div>
              </div>
            </div>
          </div>

          <div className="mb-12">
            <h2 className="text-2xl font-bold text-gray-900 mb-6">Clients</h2>
            <div className="mb-6 flex items-center gap-3">
              <div className="relative w-full max-w-md">
                <input
                  type="text"
                  value={clientSearch}
                  onChange={(e) => setClientSearch(e.target.value)}
                  placeholder="Rechercher par nom ou prénom..."
                  className="w-full px-4 py-3 bg-white/50 border border-gray-200 rounded-lg focus:ring-2 focus:ring-black focus:border-black focus:outline-none transition-all duration-200 text-gray-800 placeholder-gray-400"
                />
                <Search className="absolute right-3 top-1/2 transform -translate-y-1/2 h-5 w-5 text-gray-400" />
              </div>
            </div>
            <div className="bg-white/30 backdrop-blur-lg border border-gray-200/50 rounded-xl shadow-lg overflow-x-auto">
              <table className="min-w-full divide-y divide-gray-200">
                <thead className="bg-gray-50/50">
                  <tr>
                    <th className="px-6 py-3 text-left text-sm font-bold text-gray-800">Nom</th>
                    <th className="px-6 py-3 text-left text-sm font-bold text-gray-800">Prénom</th>
                    <th className="px-6 py-3 text-left text-sm font-bold text-gray-800">Email</th>
                    <th className="px-6 py-3 text-left text-sm font-bold text-gray-800">Date de création</th>
                    <th className="px-6 py-3 text-left text-sm font-bold text-gray-800">Actions</th>
                  </tr>
                </thead>
                <tbody className="divide-y divide-gray-200">
                  {filteredClients.length > 0 ? (
                    filteredClients.map((client) => (
                      <tr
                        key={client.id}
                        className="hover:bg-gray-100 transition-all duration-200 cursor-pointer"
                        onClick={() => handleUserClick(client)}
                      >
                        <td className="px-6 py-4 text-sm text-gray-800">{client.lastName}</td>
                        <td className="px-6 py-4 text-sm text-gray-800">{client.firstName}</td>
                        <td className="px-6 py-4 text-sm text-gray-800">{client.email}</td>
                        <td className="px-6 py-4 text-sm text-gray-800">{client.createdAt}</td>
                        <td className="px-6 py-4">
                          <button
                            onClick={(e) => {
                              e.stopPropagation();
                              handleDeleteUser(client.id, "client");
                            }}
                            className="bg-red-500 text-white px-4 py-2 rounded-lg flex items-center gap-2 hover:bg-red-600 transition-all duration-200 shadow-md"
                          >
                            <Trash2 className="h-4 w-4" /> Supprimer
                          </button>
                        </td>
                      </tr>
                    ))
                  ) : (
                    <tr>
                      <td colSpan={5} className="px-6 py-4 text-center text-sm text-gray-500">
                        Aucun client trouvé.
                      </td>
                    </tr>
                  )}
                </tbody>
              </table>
            </div>
          </div>

          <div>
            <h2 className="text-2xl font-bold text-gray-900 mb-6">Vendeurs</h2>
            <div className="mb-6 flex items-center gap-3">
              <div className="relative w-full max-w-md">
                <input
                  type="text"
                  value={sellerSearch}
                  onChange={(e) => setSellerSearch(e.target.value)}
                  placeholder="Rechercher par nom, prénom ou nom de boutique..."
                  className="w-full px-4 py-3 bg-white/50 border border-gray-200 rounded-lg focus:ring-2 focus:ring-black focus:border-black focus:outline-none transition-all duration-200 text-gray-800 placeholder-gray-400"
                />
                <Search className="absolute right-3 top-1/2 transform -translate-y-1/2 h-5 w-5 text-gray-400" />
              </div>
            </div>
            <div className="bg-white/30 backdrop-blur-lg border border-gray-200/50 rounded-xl shadow-lg overflow-x-auto">
              <table className="min-w-full divide-y divide-gray-200">
                <thead className="bg-gray-50/50">
                  <tr>
                    <th className="px-6 py-3 text-left text-sm font-bold text-gray-800">Nom</th>
                    <th className="px-6 py-3 text-left text-sm font-bold text-gray-800">Prénom</th>
                    <th className="px-6 py-3 text-left text-sm font-bold text-gray-800">Email</th>
                    <th className="px-6 py-3 text-left text-sm font-bold text-gray-800">Date de création</th>
                    <th className="px-6 py-3 text-left text-sm font-bold text-gray-800">Nom de boutique</th>
                    <th className="px-6 py-3 text-left text-sm font-bold text-gray-800">Actions</th>
                  </tr>
                </thead>
                <tbody className="divide-y divide-gray-200">
                  {filteredSellers.length > 0 ? (
                    filteredSellers.map((seller) => (
                      <tr
                        key={seller.id}
                        className="hover:bg-gray-100 transition-all duration-200 cursor-pointer"
                        onClick={() => handleUserClick(seller)}
                      >
                        <td className="px-6 py-4 text-sm text-gray-800">{seller.lastName}</td>
                        <td className="px-6 py-4 text-sm text-gray-800">{seller.firstName}</td>
                        <td className="px-6 py-4 text-sm text-gray-800">{seller.email}</td>
                        <td className="px-6 py-4 text-sm text-gray-800">{seller.createdAt}</td>
                        <td className="px-6 py-4 text-sm text-gray-800">{seller.shopName}</td>
                        <td className="px-6 py-4 flex gap-2">
                          <button
                            onClick={(e) => {
                              e.stopPropagation();
                              handleWarnSeller(seller.id, `${seller.firstName} ${seller.lastName}`);
                            }}
                            className="bg-yellow-500 text-white px-4 py-2 rounded-lg flex items-center gap-2 hover:bg-yellow-600 transition-all duration-200 shadow-md"
                          >
                            <AlertTriangle className="h-4 w-4" /> Avertir
                          </button>
                          <button
                            onClick={(e) => {
                              e.stopPropagation();
                              handleDeleteUser(seller.id, "seller");
                            }}
                            className="bg-red-500 text-white px-4 py-2 rounded-lg flex items-center gap-2 hover:bg-red-600 transition-all duration-200 shadow-md"
                          >
                            <Trash2 className="h-4 w-4" /> Supprimer
                          </button>
                        </td>
                      </tr>
                    ))
                  ) : (
                    <tr>
                      <td colSpan={6} className="px-6 py-4 text-center text-sm text-gray-500">
                        Aucun vendeur trouvé.
                      </td>
                    </tr>
                  )}
                </tbody>
              </table>
            </div>
          </div>

          {/* Modale de confirmation pour la suppression */}
          {showDeleteModal && (
            <div className="fixed inset-0 bg-black/60 flex items-center justify-center z-50 animate-fade-in">
              <div className="bg-white/30 backdrop-blur-lg border border-gray-200/50 p-6 rounded-xl shadow-2xl max-w-sm w-full">
                <h3 className="text-xl font-semibold text-gray-900 mb-4">
                  Confirmer la suppression
                </h3>
                <p className="text-gray-600 mb-6">
                  Êtes-vous sûr de vouloir supprimer cet utilisateur ?
                </p>
                <div className="flex justify-end gap-3">
                  <button
                    onClick={cancelDelete}
                    className="bg-gray-200 text-gray-800 px-4 py-2 rounded-lg font-semibold hover:bg-gray-300 transition-all duration-300"
                  >
                    Annuler
                  </button>
                  <button
                    onClick={confirmDelete}
                    className="bg-red-500 text-white px-4 py-2 rounded-lg font-semibold hover:bg-red-600 transition-all duration-300"
                  >
                    Confirmer
                  </button>
                </div>
              </div>
            </div>
          )}

          {/* Carte de détails de l'utilisateur */}
          {selectedUser && (
            <div className="fixed inset-0 bg-black/70 backdrop-blur-md flex items-center justify-center z-50 animate-fade-in">
              <div className="bg-white rounded-2xl shadow-2xl max-w-lg w-full p-6 transform transition-all duration-300 scale-100 hover:scale-105">
                <div className="relative">
                  <button
                    onClick={closeUserDetails}
                    className="absolute top-2 right-2 text-gray-500 hover:text-gray-800 transition-colors duration-200"
                  >
                    ✕
                  </button>
                  <div className="flex flex-col items-center">
                    <img
                      src={selectedUser.image || "https://via.placeholder.com/150"}
                      alt={`${selectedUser.firstName} ${selectedUser.lastName}`}
                      className="w-32 h-32 rounded-full object-cover border-4 border-gray-200 shadow-lg mb-4"
                    />
                    <h3 className="text-2xl font-bold text-gray-900">
                      {selectedUser.firstName} {selectedUser.lastName}
                    </h3>
                    <p className="text-gray-600 mb-4">{selectedUser.email}</p>

                    <div className="w-full space-y-3">
                      <div className="flex justify-between items-center bg-gray-50 p-3 rounded-lg">
                        <span className="text-gray-700 font-semibold">Date de création :</span>
                        <span className="text-gray-900">{selectedUser.createdAt}</span>
                      </div>
                      <div className="flex justify-between items-center bg-gray-50 p-3 rounded-lg">
                        <span className="text-gray-700 font-semibold">Téléphone :</span>
                        <span className="text-gray-900">{selectedUser.phone || "Non renseigné"}</span>
                      </div>
                      <div className="flex justify-between items-center bg-gray-50 p-3 rounded-lg">
                        <span className="text-gray-700 font-semibold">Adresse :</span>
                        <span className="text-gray-900">
                          {selectedUser.address
                            ? `${selectedUser.address.street}, ${selectedUser.address.province}, ${selectedUser.address.postalCode}`
                            : "Non renseignée"}
                        </span>
                      </div>
                      {"shopName" in selectedUser && (
                        <>
                          <div className="flex justify-between items-center bg-purple-50 p-3 rounded-lg">
                            <span className="text-purple-700 font-semibold">Nom de la boutique :</span>
                            <span className="text-purple-900 font-bold">{selectedUser.shopName}</span>
                          </div>
                          <div className="flex justify-between items-center bg-red-50 p-3 rounded-lg">
                            <span className="text-red-700 font-semibold">Signalements :</span>
                            <span className="text-red-900 font-bold">{selectedUser.reports || 0}</span>
                          </div>
                        </>
                      )}
                    </div>
                  </div>
                </div>
              </div>
            </div>
          )}
        </div>
      </div>

      {/* Styles personnalisés */}
      <style jsx>{`
        @keyframes slide-in {
          0% {
            width: 4rem; /* w-16 */
            opacity: 0.9;
          }
          100% {
            width: 16rem; /* w-64 */
            opacity: 1;
          }
        }
        @keyframes slide-out {
          0% {
            width: 16rem; /* w-64 */
            opacity: 1;
          }
          100% {
            width: 4rem; /* w-16 */
            opacity: 0.9;
          }
        }
        aside {
          animation: ${isSidebarOpen ? "slide-in" : "slide-out"} 0.5s ease-in-out forwards;
        }
        @keyframes fade-in {
          from {
            opacity: 0;
          }
          to {
            opacity: 1;
          }
        }
        @keyframes bounce-in {
          0% {
            transform: scale(0.9);
            opacity: 0;
          }
          60% {
            transform: scale(1.05);
            opacity: 1;
          }
          100% {
            transform: scale(1);
          }
        }
        .animate-fade-in {
          animation: fade-in 0.5s ease-in-out;
        }
        .animate-bounce-in {
          animation: bounce-in 0.5s ease-in-out;
        }
      `}</style>
    </div>
  );
}